@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
.countdown-row {
    margin-top: 1.25rem;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.modal-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}
.tabcolor {
    background: #fff !important;
}
._main-container {
    padding: 2rem;
    background: #fff;
}

.main-container {
    margin: 2rem 0 !important;
}

.image-container h1 {
    display: none;
    text-align: center;
    margin-bottom: 1rem;
}

.tabs-container{
    margin-top: 1.5rem;
}

@media (max-width: 768px) {
    .info-container {
        margin-top: 1rem;
    }

    .info-container h1 {
        display: none;
    }

    .image-container h1 {
        display: block;
    }
  }
